<template>
  <main class="mt-5">
    <div class="container">
      <div class="row justify-content-center">
           <div class="col-md-8">
            <h4 class="text-center mb-5">Order Detail</h4>
            <table class="table table-bordered mb-5">
            <thead>
                <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                </tr>
            </thead>
            <tbody>
                <tr :key="i" v-for="(order, i) in orderDetail">
                <td class="align-middle"><img :src="`/download/${order.id}/${order.path}`" style="height:auto; width:40px;" /></td>
                <td class="align-middle">{{order.product_name}}</td>
                <td class="align-middle">{{getCurrencyFormat(order.product_price)}}</td>
                <td class="align-middle">
                    <div>
                    {{order.quantity}}
                    </div>
                </td>
                <td class="align-middle">
                    <div>
                        {{getCurrencyFormat(order.totalPrice)}}
                    </div>
                </td>
                </tr>
            </tbody>
            </table>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"></label>
                <label class="col-md-9 col-form-label">{{orderDetail[0].created_date}}</label>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"></label>
                <label class="col-md-9 col-form-label">{{orderDetail[0].order_id}}</label>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"> </label>
                <label class="col-md-9 col-form-label">{{orderDetail[0].email}}</label>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"> </label>
                <label class="col-md-9 col-form-label">{{orderDetail[0].name_of_depositor}}</label>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"> </label>
                <label class="col-md-9 col-form-label">{{getCurrencyFormat(orderDetail[0].payment_amount)}}</label>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"> </label>
                <label class="col-md-9 col-form-label">{{orderDetail[0].name}}</label>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"></label>
                <label class="col-md-9 col-form-label">{{orderDetail[0].address}}</label>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"></label>
                <label class="col-md-9 col-form-label">{{orderDetail[0].phone}}</label>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"></label>
                <label class="col-md-9 col-form-label">{{orderDetail[0].requested_term}}</label>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"></label>
                <div class="col-md-9">
                    <select class="form-select" v-model="orderDetail[0].delivery_status">
                        <option :key="i" v-for="(status, i) in statusList">{{status.status}}</option>
                    </select>
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"></label>
                <div class="col-md-9">
                    <input type="text" class="form-control" v-model="orderDetail[0].transport_document_number">
                </div>
            </div>
            <div class="row mt-5">
                <div class="d-flex justify-content-between align-items-center">
                    <div class="col-6 d-grid p-1">
                        <button type="button" class="btn btn-lg btn-outline-dark" @click="goToList">CANCEL</button>
                    </div>
                    <div class="col-6 d-grid p-1">
                        <button type="button" class="btn btn-lg btn-outline-dark" @click="goToUpdate(this.orderId)">SAVE</button>
                    </div>
                </div>
            </div>
          </div>
      </div>
    </div>
  </main>
</template>

<script>
export default {
  data() {  //  data template     data 
    return {
      orderId: 0,
      orderDetail: {},
      statusList: []
    };
  },
  created() {
    this.orderId = this.$route.query.order_id;  // path order_id orderId 
    this.getOrderDetail();  // created  getOrderDetail  data  
    this.getStatusList(); // created  getStatusList  data  
  },
  methods: {      //  
    getCurrencyFormat(value) {  //  ,  $currencyFormat 
      return this.$currencyFormat(value);
    },
    async getOrderDetail() {    // getCartList  
      let orderDetail = await this.$api("/api/orderDetail", {param:[this.orderId]});  // url  app.js /api/:alias  orderId  sql cartList  data 
      this.orderDetail = orderDetail
      console.log(this.orderDetail); //    
    },
    async getStatusList() {
      let statusList = await this.$api("/api/statusList");  //  sql  status data 
      this.statusList = statusList;
    },
    goToList() {
      this.$router.push({path:'/order_management'});  //    , /order_management 
    },
    async goToUpdate(order_id) {  // order_id  sweetAlert2 
      this.$swal.fire({
        title: ' ?',
        showCancelButton: true,
        confirmButtonText: 'MODIFY',
        cancelButtonText: 'CANCEL'
      }).then(async (result) => {
        if (result.isConfirmed) {
            await this.$api("/api/orderUpdate", {param:[this.orderDetail[0].delivery_status, this.orderDetail[0].transport_document_number, order_id]});  // status,  , order_id data   sql orderUpdate   
            this.$swal.fire('Saved.', '', 'success');
            this.$router.push({path:'/order_management'});  // /order_management 
        }
      });
    }
  }
}
</script>