<template>
  <main class="mt-3">
        <div class="container">
          <div class="row justify-content-center">
            <div class="col-md-8">
            <h2 class="text-center mb-5"> </h2>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"></label>
                <div class="col-md-9">
                  <input type="text" class="form-control" v-model="productDetail.product_name">
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"> </label>
                <div class="col-md-9">
                  <div class="input-group">
                    <input type="number" class="form-control" v-model="productDetail.product_price">
                    <span class="input-group-text"></span>
                  </div>
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"> </label>
                <div class="col-md-9">
                  <textarea class="form-control" rows="7" v-model="productDetail.product_description"></textarea>                  
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"></label>
                <div class="col-md-9">
                  <input type="text" class="form-control" v-model="productDetail.product_color">
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"></label>
                <div class="col-md-9">
                  <input type="text" class="form-control" v-model="productDetail.product_fabric">
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"> </label>
                <div class="col-md-9">
                  <input type="text" class="form-control" v-model="productDetail.product_model">
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"></label>
                <div class="col-md-9">
                  <input type="text" class="form-control" v-model="productDetail.product_size">
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"> </label>
                <div class="col-md-9">
                  <textarea class="form-control" rows="3" v-model="productDetail.product_sizeGuide"></textarea>               
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label"></label>
                <div class="col-md-9">
                  <input type="text" class="form-control" v-model="productDetail.tags">
                </div>
            </div>                   
            <div class="mb-3 row">
                <div class="d-flex justify-content-between align-items-center">
                    <div class="col-6 d-grid p-1">
                        <button type="button" class="btn btn-lg btn-outline-dark" @click="goToList">CANCEL</button>
                    </div>
                    <div class="col-6 d-grid p-1">
                        <button type="button" class="btn btn-lg btn-outline-dark" @click="productUpdate">SAVE</button>
                    </div>
                  </div>
            </div>
          </div>
          </div>
        </div>
      </main>
</template>

<script>
export default {
  data() {  //  data template     data 
    return {
      productId: 0,
      productDetail: {},
    };
  },
  computed: {
    user() {
      return this.$store.state.user;  // user     user() 
    }
  },
  mounted() {
    if(this.user.email == undefined) {  // user email    
      alert("    .");
      this.$router.push({path:'/'});
    }
  },
  created() {
    this.productId = this.$route.query.product_id;  // path  product_id  
    this.getProductDetail();  // created  getProductDetail  data  
  },
  methods: {
    goToList() {
      this.$router.push({path:'/sales'}); // /sales 
    },
    async getProductDetail() {    // getProductDetail  
      let productDetail = await this.$api("/api/productDetail2",{param:[this.productId]});  // url  app.js /api/:alias  productId    sql productDatail data 
      if (productDetail.length > 0) { // productDetail   
        this.productDetail = productDetail[0];  // productDetail  
      }
    },
    productUpdate() {    
      if(this.productDetail.product_name == "") { //      alert 
        return this.$swal("  .");
      }

      if(this.productDetail.product_price == "" || this.productDetail.product_price == 0) { //    0   alert 
        return this.$swal(" .");
      }

      if(this.productDetail.product_description == "") {  //     0   alert 
        return this.$swal("  .");
      }

      if(this.productDetail.product_color == "") {  //    0   alert 
        return this.$swal("  .");
      }

      if(this.productDetail.product_fabric == "") { //    0   alert 
        return this.$swal("  .");
      }
      
      if(this.productDetail.product_model == "") {  //      0   alert 
        return this.$swal("    .");
      }

      if(this.productDetail.product_size == "") { //    0   alert 
        return this.$swal("  .");
      }

      if(this.productDetail.product_sizeGuide == "") {  //     0   alert 
        return this.$swal("   .");
      }  
      this.$swal.fire({
            title: ' ?',
            showCancelButton: true,
            confirmButtonText: 'MODIFY',
            cancelButtonText: 'CANCEL'
        }).then(async (result) => {
            if (result.isConfirmed) {
                await this.$api("/api/productUpdate", {param:[this.productDetail, this.productId]});  // productDetail, productId data  , sql productUpdate  data 
                this.$swal.fire('Saved.', '', 'success');
                this.$router.push({path:'/sales'}); // /sales 
            }
        });
    }
  }
}
</script>