<template>
  <main class="mt-3">
        <div class="container">
          <div class="row justify-content-center">
            <div class="col-md-8">
            <h4 class="text-center mb-5">Membership information</h4>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label">E-mail</label>
                <div class="col-md-9">
                  <input type="text" class="form-control" disabled readonly v-model="userInfo.email">
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label">Password</label>
                <div class="col-md-9">
                  <input type="password" class="form-control" v-model="userInfo.password">
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label">Name</label>
                <div class="col-md-9">
                  <input type="text" class="form-control" v-model="userInfo.name">
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label">Address</label>
                <div class="col-md-9">
                  <input type="text" class="form-control" v-model="userInfo.address">
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label">Phone</label>
                <div class="col-md-9">
                  <input type="text" class="form-control" v-model="userInfo.phone">
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label">Account holder</label>
                <div class="col-md-9">
                  <input type="text" class="form-control" v-model="userInfo.account_holder">
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label">Bank name</label>
                <div class="col-md-9">
                    <select class="form-select" v-model="userInfo.bank_name">
                        <option :key="i" v-for="(bank, i) in bankList">{{bank.bank_name}}</option>
                    </select>
                </div>
            </div>
            <div class="mb-5 row">
                <label class="col-md-3 col-form-label">Bank account number</label>
                <div class="col-md-9">
                  <input type="text" class="form-control" v-model="userInfo.bank_account_number">
                </div>
            </div>                
            <div class="row">
                <div class="d-flex justify-content-between align-items-center">
                    <div class="col-6 d-grid p-1">
                        <button type="button" class="btn btn-lg btn-outline-dark" @click="goToList">CANCEL</button>
                    </div>
                    <div class="col-6 d-grid p-1">
                        <button type="button" class="btn btn-lg btn-outline-dark" @click="productUpdate">SAVE</button>
                    </div>
                  </div>
            </div>
          </div>
          </div>
        </div>
      </main>
</template>

<script>
export default {
  data() {  //  data template     data 
    return {
      user_email: "",
      userInfo: {},
      bankList: [],
    };
  },
  computed: {
    user() {
      return this.$store.state.user;  // user     user() 
    }
  },
  mounted() {
    if(this.user.email == undefined) {  // user email    
      alert("    .");
      this.$router.push({path:'/'});
    }
  },
  created() {
    this.user_email = this.$store.state.user.email; //   email user_email 
    this.getUserInfo(); // created  getUserInfo  data  
    this.getBankList(); // created  getBankList  data  
  },
  methods: {
    goToList() {
      this.$router.push({path:'/mypage'});  //     /mymage 
    },
    async getUserInfo() {    // getUserInfo  
      let userInfo = await this.$api("/api/userInfo",{param:[this.user_email]});  // url  app.js /api/:alias  user_email    sql userInfo data 
      if (userInfo.length > 0) { // userInfo   
        this.userInfo = userInfo[0];  // userInfo  
      }
    },
    async getBankList() {
      let bankList = await this.$api("/api/bankList", {});  // sql bankList  bank data 
      this.bankList = bankList;
    },
    productUpdate() {    
      if(this.userInfo.password == "") {  //      alert 
        return this.$swal("  .");
      }

      if(this.userInfo.name == "") {  //      alert 
        return this.$swal("  .");
      }

      if(this.userInfo.address == "") { //      alert 
        return this.$swal("  .");
      }
      
      if(this.userInfo.phone == "") { //       alert 
        return this.$swal("   .");
      }

      if(this.userInfo.account_holder == "") {  //      alert 
        return this.$swal("  .");
      }
      
      if(this.userInfo.bank_account_number == "") { //      alert 
        return this.$swal("  .");
      }
      this.$swal.fire({
            title: ' ?',
            showCancelButton: true,
            confirmButtonText: 'MODIFY',
            cancelButtonText: 'CANCEL'
        }).then(async (result) => {
            if (result.isConfirmed) {
                await this.$api("/api/userInfoUpdate", {user: this.userInfo});  // userInfo   sql userInfoUpdate   
                this.$swal.fire('Saved.', '', 'success');
                this.$router.push({path:'/mypage'});  // /mypage 
            }
        });
    }
  }
}
</script>