<template>
  <main class="mt-5">
    <div class="container">
      <div v-if="productList == 0" class="row">
          <h5 class="m-4">  .</h5>
      </div>
      <div v-else class="row">
        <div class="col-xl-3 col-lg-4 col-md-6" :key="i" v-for="(product, i) in productList">
          <div class="card text-start" style="width: 18rem; border: 0px;">
            <a @click="goToDetail(product.id);" style="cursor:pointer;"><img :src="`/download/${product.id}/${product.path}`" class="card-img-top" alt="..."></a>
            <div class="card-body">
              <h5 class="card-title">{{product.product_name}}</h5>
              <p class="card-text">{{getCurrencyFormat(product.product_price)}}</p>
            </div>
          </div>
        </div>    
      </div>
    </div>
  </main>
</template>

<script>
export default {
  data() {  //  data template     data 
    return {
      productList: []
    };
  },
  created() {     // created     data  
    this.getProductList();
  },
  methods: {      //  
    getCurrencyFormat(value) {  // $currencyFormat 
      return this.$currencyFormat(value);
    },
    async getProductList() {    // getProductList  
      this.productList = await this.$api("/api/accList",{});  // url  app.js /api/:alias  sql accList data 
      console.log(this.productList); //    
    },
    goToDetail(product_id) {  //    product_id    router 
      this.$router.push({path:'/detail', query:{product_id:product_id}}); // path /detail  product_id    
    }
  }
}
</script>