<template>
  <main class="mt-5">
    <div class="container">
      <div class="row justify-content-center">
        <div class="col-md-4">
          <div>
            <h5 class="pb-3 text-start">RETURNING CUSTOMERS</h5>
            <div class="form-floating mb-3">
              <input type="email" class="form-control" placeholder="EMAIL ADDRESS" v-model="t_user.email">
              <label for="floatingInput">EMAIL ADDRESS</label>
            </div>
            <div class="form-floating mb-3">
              <input type="password" class="form-control" placeholder="PASSWORD" v-model="t_user.password">
              <label for="floatingPassword">PASSWORD</label>
            </div>
            <div class="pt-2 float-start">
              <button type="button" class="btn btn-dark me-1" @click="login">LOG IN</button>
              <a class="btn btn-dark me-3" href="/join" role="button">JOIN US</a>
              <img src="http://papaspick.com/web/upload/2019_web/icon/kakao_login.jpg" class="img-fluid rounded-2 cursor-pointer" style="width: 175px; height: 38px; cursor:pointer;" @click="kakaoLogin">
            </div>
          </div>
        </div>
        <!-- <ul class="nav justify-content-center mt-3  ">
          <li class="nav-item">
            <a class="nav-link  text-dark" href="#"> FORGOT ID</a>
          </li>
          <li class="nav-item"> 
            <a class="nav-link  text-dark" href="#"> FORGOT PASSWORD</a>
          </li>
        </ul> -->
      </div>
    </div>
  </main>
</template>

<script>
export default {
  data() {
    return {
      t_user: {
        email: "",
        password: ""
      }
    };
  },
  computed: {
    user() {
      return this.$store.state.user;  // user     user() 
    }
  },
  methods: {
    async login() {
      await this.$api('/api/login', {user: this.t_user})  // t_user  login sql  
      .then(() => {
        alert(" !");  // api     alert 
        this.$router.push({path:'/'});  //  
        this.$store.commit("user", this.t_user);  // vuex   store user  
      }).catch(() => {  // api     alert 
        alert("  .");
      });
    },
    kakaoLogin() {
      window.Kakao.Auth.login({
        scope: 'profile_nickname, account_email, gender', //   
        success: this.getProfile  //   getProfile() 
      });
    },
    getProfile(authObj) { // authObj   
      console.log(authObj);
      window.Kakao.API.request({  //    
        url: '/v2/user/me',
        success: res => { //  
          const kakao_account = res.kakao_account;  //     
          console.log(kakao_account); //   
          this.kakao_login(kakao_account);  //   kakao_login 
          this.$router.push({path:'/'});  //  
          alert(" !");  //  alert 
        }
      });
    },
    async kakao_login(kakao_account) {  // login signup
      await this.$api("/api/kakaoLogin", {  //   kakaoLogin sql  
        param: [
          {email:kakao_account.email, name:kakao_account.profile.nickname},
          {name:kakao_account.profile.nickname}
        ]
      });

      this.$store.commit("user", kakao_account);  // vuex   store user  
    },
    kakaoLogout() {
      window.Kakao.Auth.logout((response) => {
        console.log(response);
        this.$store.commit("user", {}); // store user  data  
        // this.$router.go();
        alert("");
      });
    }
  }
}
</script> 