<template>
  <main class="mt-5">
    <div class="container">
      <h4 class="text-center mb-5">User Management</h4>
      <div>
        <table class="table table-bordered">
          <thead>
            <tr>
              <th>E-mail</th>
              <th>Name</th>
              <th>Address</th>
              <th>Phone number</th>
              <th>Account holder</th>
              <th>Bank name</th>
              <th>Bank account number</th>
            </tr>
          </thead>
          <tbody>
            <tr :key="i" v-for="(user, i) in userInfo">
              <td class="align-middle">{{user.email}}</td>
              <td class="align-middle">{{user.name}}</td>
              <td class="align-middle">{{user.address}}</td>
              <td class="align-middle">{{user.phone}}</td>
              <td class="align-middle">{{user.account_holder}}</td>
              <td class="align-middle">{{user.bank_name}}</td>
              <td class="align-middle">{{user.bank_account_number}}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </main>
</template>

<script>
export default {
  data() {  //  data template     data 
    return {
      userInfo: {}
    };
  },
  created() {
    this.getUserInfo(); // created  getUserInfo  data  
  },
  methods: {      //  
    async getUserInfo() {    // getUserInfo  
      let userInfo = await this.$api("/api/userManagement");  // url  app.js /api/:alias  sql userManagement  data 
      this.userInfo = userInfo
      console.log(this.userInfo); //    
    }
  }
}
</script>