<template>
  <main class="mt-5">
    <div class="container">
      <h4 class="mb-3"></h4>
      <div>
        <table class="table table-bordered mb-5">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr :key="i" v-for="(product, i) in cartList">
              <td><img :src="`/download/${product.id}/${product.path}`" style="height:50px; width:auto;" /></td>
              <td>{{product.product_name}}</td>
              <td>{{getCurrencyFormat(product.product_price)}}</td>
              <td>
                <div>
                  {{product.quantity}}
                </div>
              </td>
              <td>
                <div>
                  {{getCurrencyFormat(product.totalPrice)}}
                </div>
              </td>
            </tr>
            <tr>
              <th colspan="6" style="text-align: right; font-size: large;">  {{getCurrencyFormat(total)}} +  {{getCurrencyFormat(deliveryPrice)}} =   {{getCurrencyFormat(paymentAmount)}}</th>
            </tr>
          </tbody>
        </table>
        <div class="row justify-content-evenly">
          <div class="col-md-6">
            <h5 class="text-center ms-4 mb-3"> </h5>
            <div class="mb-3 row">
              <label class="col-md-3 col-form-label"></label>
              <div class="col-md-9">
                <input type="text" class="form-control" v-model="userInfo.address">
              </div>
            </div>
            <div class="mb-3 row">
              <label class="col-md-3 col-form-label"> </label>
              <div class="col-md-9">
                <input type="text" class="form-control" v-model="userInfo.name">
              </div>
            </div>
            <div class="mb-3 row">
              <label class="col-md-3 col-form-label"></label>
              <div class="col-md-9">
                <input type="text" class="form-control"  v-model="userInfo.phone">
              </div>
            </div>
            <div class="mb-3 row">
              <label class="col-md-3 col-form-label"> </label>
              <div class="col-md-9">
                <textarea class="form-control" rows="4" v-model="orderInfo.requested_term"></textarea>
              </div>
            </div>
          </div>
          <div class="col-md-3">
            <h5 class="text-center ms-4 mb-3"> </h5>
            <label class="col-md-12 col-form-label text-start mb-2"> </label>
            <div class="col-md-12 mb-3">
              <select class="form-select">
                <option selected>-   -</option>
                <option>{{sellerInfo.bank}}</option>
              </select>
            </div>
            <div class="col-md-12">
                <input type="text" class="form-control" placeholder="" v-model="orderInfo.name_of_depositor">
            </div>
          </div>
        </div>
        <div class="col-12 mt-5">
          <button type="button" class="btn btn-outline-dark"  @click="orderPayment"></button>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
export default {
  data() {  //  data template     data 
    return {
      cartList: {},
      cartId:"",
      total: 0,
      deliveryPrice: 0,
      paymentAmount: 0,
      userInfo: {},
      sellerInfo: {},
      orderInfo: {}
    };
  },
  created() {
    this.cartId = this.$store.state.user.email; // store   cartId 
    this.getCartList(); // created  getCartList  data  
    this.getUserInfo(); // created  getUserInfo  data  
    this.getSellerInfo(); // created  getSellerInfo  data  
  },
  methods: {      //  
    getCurrencyFormat(value) {  //  ,  $currencyFormat 
      return this.$currencyFormat(value);
    },
    async getUserInfo() {    // getUserInfo  
      let userInfo = await this.$api("/api/userInfo",{param:[this.cartId]});  // url  app.js /api/:alias  cartId  , sql userInfo  data 
      if (userInfo.length > 0) { // UserInfo   
        this.userInfo = userInfo[0];  // UserInfo  
      }
    },
    async getSellerInfo() {    // getSellerInfo  
      let sellerInfo = await this.$api("/api/sellerInfo");  // url  app.js /api/:alias  sql sellerInfo  data 
      if (sellerInfo.length > 0) { // sellerInfo   
        this.sellerInfo = sellerInfo[0];  // sellerInfo  
      }
    },
    async getCartList() {    // getCartList  
      let cartList = await this.$api("/api/cartList",{param:[this.cartId]});  // url  app.js /api/:alias  cartId  , sql cartList  data 
      this.cartList = cartList
      console.log(this.cartList); //    

      let total = 0;
      cartList.forEach(item => {
        total += item.totalPrice; //      
      });
      this.total = total;

      let deliveryPrice = 0;
      if (total >= 100000) {  // total  10   0
        deliveryPrice = 0;
      }
      else {
        deliveryPrice = 3000; //    3000
      }
      this.deliveryPrice = deliveryPrice

      let paymentAmount = total + deliveryPrice;  // total       
      this.paymentAmount = paymentAmount;
    },
    async orderPayment() {

      this.$swal.fire({
            title: ' ?',
            showCancelButton: true,
            confirmButtonText: 'PAYMENT',
            cancelButtonText: 'CANCEL'
        }).then(async (result) => {
            if (result.isConfirmed) {
                await this.$api("/api/orderPayment", {param:[this.userInfo.address, this.userInfo.name, this.userInfo.phone, this.orderInfo.requested_term, this.orderInfo.name_of_depositor, this.$store.state.user.email, this.paymentAmount]});  //  data  , sql orderPayment  data Insert
                this.$api("/api/cartCopy", {param:[this.$store.state.user.email]}); //  email data  , sql cartCopy  data 
                this.$api("/api/deleteCart", {param:[this.$store.state.user.email]}); //  email data  , sql deleteCart  data 
                this.$swal.fire('Complete.', '', 'success');
                this.$router.push({path:'/'});  //  
            }
        });
    }
  }
}
</script>